.App{
    min-height: 100vh;
    background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
}

.search-form{
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search-bar{
    width: 50%;
    border: none;
    padding: 10px;
}
.search-btn{
    background: lightcoral;
    border:none;
    padding: 10px;
    color:white;
}
.rece{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}